@import url(https://fonts.googleapis.com/css?family=Roboto:300);
body {
  margin: 0;
  font-family: "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
hr {
  width: 80%;
  border-top: 0.5px solid #f1f2f6;
}
::-webkit-scrollbar {
  width: 0px;
}

.wrapper {
  width: 100vw;
  height: 100vh;
  background: #f1f2f6;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.login-page {
  width: 360px;
  padding: 8% 0 0;
  margin: auto;
}
.login-name-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 45px;
}
.login-app-logo {
  height: 68px;
}
.login-app-name {
  font-size: 58px;
  color: #2f3542;
}
.form {
  position: relative;
  z-index: 1;
  background: #ffffff;
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form input {
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}
.form button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #57606f;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #ffffff;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.form button:hover,
.form button:active,
.form button:focus {
  background: #2f3542;
}
.form .message {
  margin: 15px 0 0;
  color: #b3b3b3;
  font-size: 12px;
}
.form .message a {
  color: #2f3542;
  text-decoration: none;
}
.container {
  position: relative;
  z-index: 1;
  max-width: 300px;
  margin: 0 auto;
}
.container:before,
.container:after {
  content: "";
  display: block;
  clear: both;
}
.container .info {
  margin: 50px auto;
  text-align: center;
}
.container .info h1 {
  margin: 0 0 15px;
  padding: 0;
  font-size: 36px;
  font-weight: 300;
  color: #1a1a1a;
}
.container .info span {
  color: #4d4d4d;
  font-size: 12px;
}
.container .info span a {
  color: #000000;
  text-decoration: none;
}
.container .info span .fa {
  color: #ef3b3a;
}

.page {
  margin-bottom: 60px;
}

.nav-bar {
  padding: 10px;
  background-color: #f1f2f6;
  display: flex;
  align-items: center;
}
.app-logo {
  height: 22px;
}
.app-name {
  font-size: 18px;
  color: #2f3542;
}

.header {
  margin: 60px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  border: #f1f2f6 solid 0.5px;
  border-radius: 3px;
}
.header-item {
  height: 100%;
  padding: 10px;
  align-items: center;
}
.header-item > span {
  display: block;
}
.header-title {
  font-size: 16px;
  font-weight: 900;
  color: #2f3542;
  padding: 3px 0px;
}
.header-body {
  font-size: 36px;
  color: #57606f;
  padding: 3px 0px;
}
.header-footer {
  font-size: 16px;
  color: #ced6e0;
}

.queue {
  margin: 0px 60px;
  display: flex;
  flex-direction: column;
  border: #f1f2f6 solid 0.5px;
  border-radius: 3px;
}
.queue-header {
  padding: 25px 15px 10px;
  flex-grow: 1;
}
.queue-name-container {
  flex-direction: row;
}
.queue-name {
  font-size: 22px;
  color: #57606f;
  margin-left: 25px;
}
.queue-states {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 60px;
  align-items: center;
  margin-top: 20px;
}
.queue-state {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0px 10px;
  margin: 0px 25px;
  text-align: center;
  cursor: pointer;
  border-radius: 3px;
}
.queue-state-value {
  padding: 5px 10px;
  margin: 0px 5px;
  font-size: 18px;
  font-weight: 600;
  color: #ced6e0;
  border: #f1f2f6 solid 0.5px;
  border-radius: 50%;
}
.queue-state-name {
  display: inline-block;
  font-size: 18px;
  font-weight: 700;
  color: #b1becd;
  text-transform: capitalize;
}
.queue-state-active .queue-state-name {
  color: #2f3542;
}
.queue-state-active .queue-state-value {
  border: transparent solid 0.5px;
  color: #ffffff;
}
.queue-state-active .queue-state-value.active {
  background-color: #1e90ff;
}
.queue-state-active .queue-state-value.waiting {
  background-color: #ffa502;
}
.queue-state-active .queue-state-value.completed {
  background-color: #2ed573;
}
.queue-state-active .queue-state-value.failed {
  background-color: #ff4757;
}
.queue-state-active .queue-state-value.delayed {
  background-color: #eccc68;
}
.queue-state-active .queue-state-value.paused {
  background-color: #70a1ff;
}
table {
  width: 100%;
  padding: 0px 15px;
  margin-top: 10px;
}
th {
  text-align: start;
  color: #2f3542;
  padding: 0px 10px;
}
td {
  color: #57606f;
  padding: 10px;
}
.paginator {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.prev-page {
  font-size: 16px;
  color: #2f3542;
  cursor: pointer;
  text-align: start;
}
.page-count {
  color: #ced6e0;
}
.next-page {
  font-size: 16px;
  color: #2f3542;
  cursor: pointer;
  text-align: end;
}
.panel {
  max-height: 0;
  overflow: hidden;
  -webkit-transition: max-height 0.2s ease-out;
  transition: max-height 0.2s ease-out;
}
.panel-expanded {
  max-height: 525px;
}
.panel-no-expanded {
  max-height: 0;
}
.icon-arrow {
  border: solid #57606f;
  border-width: 0 1px 1px 0;
  display: inline-block;
  padding: 4px;
  cursor: pointer;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.icon-arrow.right {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}
.icon-arrow.left {
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}
.icon-arrow.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

.bar-container:after,
.bar-container:before {
  content: "";
  display: table;
  clear: both;
}
.bar-container {
  text-align: center;
}
.bar-tiny {
  font-size: 12px !important;
}
.bar-green {
  color: #ffffff !important;
  background-color: #2ed573 !important;
}
.bar-light-grey {
  background-color: #f1f2f6 !important;
}

